import noImage from '../../images/movies-card/noimage.jpg';
import { newApi } from '../api-service/apiService';
//—Ñ—É–Ω–∫—Ü—ñ—è –æ–±—Ä–æ–±–ª—è—î —ñ —Ñ–æ—Ä–º—É—î –∫–æ—Ä–µ–∫—Ç–Ω–∏–π –æ–±—î–∫—Ç —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –ø–æ–≤–µ—Ä—Ç–∞—î –ø—Ä–æ–º—ñ—Å —î –ø—Ä–æ–±–ª–µ–º–∏ –∫–æ–ª–∏ –Ω–µ–º–∞—î —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –±–∞–≥–∞—Ç–æ –ø–æ–º–∏–ª–æ–∫ –ø—Ä–æ–±–ª–µ–º–Ω—ñ –∑–∞–∫–∏–Ω—É–≤ –≤ try catch —â–µ —î –ø–æ–º–∏–ª–∫–∞ –≤—ñ–¥—Å—É—Ç–Ω–æ—Å—Ç—ñ –∫–∞—Ä—Ç–∏–Ω–∫–∏
//function insertPopularFilms(results)
//–µ—Å–ª–∏ –Ω–µ –Ω—É–∂–Ω–æ –æ–±—Ä–µ–∑–∞—Ç—å –∂–∞–Ω—Ä—ã - –≤—ã–∑–æ–≤–∏—Ç–µ —ç—Ç—É —Ñ—É–Ω–∫—Ü–∏—é —Å false: createCorectResult(results, false)

export default function createCorrectResult(results, cutting = true) {
  return new Promise(res => res(...results)).then(function () {
    //–ø–µ—Ä–µ–±–∏—Ä–∞–µ–º –º–∞—Å—Å–∏–≤ results
    results.forEach(result => {
      try {
        //–µ—Å–ª–∏ –Ω–µ—Ç release_date –ø–æ—Å—Ç–∞–≤—å first_air_date
        if (!result.release_date) {
          result.release_date = result.first_air_date;
        }
        if (!result.release_date && !result.first_air_date) {
          result.release_date = 'not defined';
        }
        // –æ–±—Ä–µ–∂—å –¥–∞—Ç—É, –æ—Å—Ç–∞–≤—å –≥–æ–¥
        if (result.release_date !== 'not defined') {
          result.release_date = result.release_date.slice(0, 4);
        }

        //–µ—Å–ª–∏ –Ω–µ—Ç original_title –ø–æ—Å—Ç–∞–≤—å original_name, –µ—Å–ª–∏ –∏ –µ–≥–æ –Ω–µ—Ç - –ø–æ—Å—Ç–∞–≤—å name
        if (!result.original_title) {
          result.original_title = result.original_name;
          if (!result.original_name) {
            result.original_title = result.name;
          }
        }

        let genres_exist = false;
        //–ø–µ—Ä–µ–±–∏—Ä–∞–µ–º id –∂–∞–Ω—Ä–æ–≤ –≤ results –∏ —Å—Ä–∞–≤–Ω–∏–≤–∞–µ–º –∏—Ö —Å –ø–æ–ª—É—á–µ–Ω–Ω—ã–º–∏ id –∏–∑ –º–∞—Å—Å–∏–≤–∞ ganres, –±–µ—Ä–µ–º name
        //–µ—Å–ª–∏ –ø—Ä–∏—Ö–æ–¥—è—Ç —Ñ–∏–ª—å–º—ã —Å id –∂–∞–Ω—Ä–æ–≤ (–Ω–µ—Ç –Ω–∞–∑–≤–∞–Ω–∏–π –∂–∞–Ω—Ä–æ–≤)
        if (!result.genres) {
          //—Å–æ–∑–¥–∞–µ–º –ø—É—Å—Ç–æ–π –º–∞—Å—Å–∏–≤ –∂–∞–Ω—Ä–æ–≤ –≤ –æ–±—ä–µ–∫—Ç–µ –º–∞—Å—Å–∏–≤–∞ results
          result.genres = [];
          //–µ—Å–ª–∏ –µ—Å—Ç—å id –∂–∞–Ω—Ä–æ–≤
          if (result.genre_ids) {
            result.genre_ids.forEach(id => {
              //–Ω–∞–π–¥–∏ –≤ –º–∞—Å—Å–∏–≤–µ –∂–∞–Ω—Ä–æ–≤ id (–ø—Ä–∏—Ö–æ–¥–∏—Ç —Å API), –∫–æ—Ç–æ—Ä—ã–π –µ—Å—Ç—å, –∏ –µ—Å–ª–∏ –µ—Å—Ç—å - –∑–∞–ø–∏—à–∏ –µ–≥–æ name –≤ –º–∞—Å—Å–∏–≤ –∂–∞–Ω—Ä–æ–≤ —Ñ–∏–ª—å–º–∞
              let genre = newApi.genresArr.find(genre => genre.id === id);
              if (genre) {
                result.genres.push(' ' + genre['name']);
                genres_exist = true; //–Ω–∞—à–ª–æ—Å—å —Å–æ–≤–ø–∞–¥–µ–Ω–∏–µ –ø–æ id –∂–∞–Ω—Ä–æ–≤
              }
            });
          } else {
            result.genres.push('not defined');
          }
          if (!genres_exist && result.genre_ids) {
            result.genres.push('not defined');
          }
        }
        //–µ—Å–ª–∏ –≤–º–µ—Å—Ç–µ —Å —Ñ–∏–ª—å–º–æ–º –ø—Ä–∏—Ö–æ–¥–∏—Ç –º–∞—Å—Å–∏–≤ —Å –∂–∞–Ω—Ä–∞–º–∏, –¥–æ—Å—Ç–∞–µ–º –Ω–∞–∑–≤–∞–Ω–∏–µ –∂–∞–Ω—Ä–∞ –∏–∑ –æ–±—ä–µ–∫—Ç–∞
        else {
          result.genres.forEach(genre => {
            genre = genre.name;
          });
        }
        //–æ–±—Ä–µ–∑–∞–µ—Ç –º–∞—Å—Å–∏–≤ –∂–∞–Ω—Ä–æ–≤ –¥–æ –¥–≤—É—Ö –ø–µ—Ä–≤—ã—Ö
        if (result.genres.length > 2 && cutting) {
          result.genres = result.genres.slice(0, 2);
          result.genres.push(' Other');
        }
        //–¥–ª—è —Ä–µ–∞–ª–∏–∑–∞—Ü–∏–∏ –∑–∞–≥–ª—É—à–∫–∏
        if (!result.poster_path) {
          result.poster_path = noImage;
        } else {
          result.poster_path =
            'https://image.tmdb.org/t/p/w500' + result.poster_path;
        }
        result.vote_average = result.vote_average.toFixed(1);
      } catch (error) {
        console.log(error);
      }
    });

    return results;
  });
}
