import axios from 'axios';
import settings from '../settings/movie-settings';
const { BASE_URL, API_KEY } = settings;
import refs from '../refs/refs';
import AuthNotifications from '../notifications/notifications';
import filterTmpl from '../../templates/filter.hbs';
const newNotification = new AuthNotifications();

class MoviesApiServiceVersion {
  constructor() {
    this.searchQuery = '';
    this.page = 1;
    //–º–∞—Å–∏–≤ –∂–∞–Ω—Ä—ñ–≤
    this.genresArr = [];
    //—Å—é–¥–∏ –∑–∞–ø–∏—Å—É—î–º —Ñ—ñ–ª—å–º –Ω–∞ —è–∫–æ–º—É –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á –∑—Ä–æ–±–∏—Ç—å –∫–ª—ñ–∫ –∑–∞–ø–∏—Å—É—î–º –ø—ñ—Å–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∫–æ—Ä–µ–∫—Ç–Ω–æ—ó –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ
    this.currentMovie = [];
    //–∑–∞–ø–∏—Å—ã–≤–∞–µ–º –∫—Ä–∏—Ç–µ—Ä–∏–∏ —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏
    this.genreCriterion = '';
    this.sortByCriterion = '';
    this.movieId = null;
    this.movie = null;
    //—Ä–æ–±–∏–º –æ–¥–∏–Ω –∑–∞–ø–∏—Ç –∑–∞ –∂–∞–Ω—Ä–∞–º–∏ –ø—Ä–∏ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—ñ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞ —ñ –∑–∞–ø–∏—Å—É—î–º –∂–∞–Ω—Ä–∏ –≤ –º–∞—Å–∏–≤ –≤–∏—â–µ
    this.getGenresMovies();
  }
  //–≤—ñ–¥–ø–æ–≤—ñ–¥—å —Ñ—ñ–ª—å–º–∏ –≤ —Ç—Ä–µ–Ω–¥—ñ
  getResponseAll(newPage) {
    // –ø–æ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—é 1 —Å—Ç–æ—Ä—ñ–Ω–∫–∞ –¥–∞–ª—å—à–µ –ø–µ—Ä–µ–¥–∞–µ–º ‚Ññ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
    let page = this.page;
    if (newPage) page = newPage;
    const results = this.manyRequest(page);
    return results;
    //–≤–∞—Ä—ñ–∞–Ω—Ç –Ω–∞ 20 –≤—ñ–¥–ø–æ–≤—ñ–¥–µ–π
    // return axios.get(
    //   `${BASE_URL}3/trending/all/day?api_key=${API_KEY}&page=${page}`,
    // );
  }
  async manyRequest(page) {
    if (document.documentElement.clientWidth > 1024) {
      const firstRequest = await axios.get(
        ` ${BASE_URL}3/movie/popular?api_key=${API_KEY}&page=${page}`,
      );
      const {
        data: { results, total_results, total_pages },
      } = firstRequest;
      try {
        if (page === total_pages) throw 'Oops, this is the last page ...'; // —Ñ—ñ–∫—Å 404 —è–∫—â–æ –æ—Å—Ç–∞–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∞ —Ç–æ 2 –∑–∞–ø—Ä–æ—Å –Ω–µ —Ä–æ–±–∏–º
        const secondRequest = await axios.get(
          ` ${BASE_URL}3/movie/popular?api_key=${API_KEY}&page=${page + 1}`,
        );
        return {
          data: {
            results: [...results, secondRequest.data.results[0]],
            total_results,
          },
        };
      } catch (error) {
        console.log(error);
        return firstRequest;
      }
    } else {
      return axios.get(
        ` ${BASE_URL}3/movie/popular?api_key=${API_KEY}&page=${page}`,
      );
    }
  }
  //–≤—ñ–¥–ø–æ–≤—ñ–¥—å –ø—Ä–∏ –ø–æ—à—É–∫—É –ø–æ —Å–ª–æ–≤—É
  getResponseWord(newPage) {
    // –ø–æ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—é 1 —Å—Ç–æ—Ä—ñ–Ω–∫–∞ –¥–∞–ª—å—à–µ –ø–µ—Ä–µ–¥–∞–µ–º ‚Ññ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
    let page = this.page;
    if (newPage) page = newPage;
    return axios.get(
      `${BASE_URL}3/search/movie?api_key=${API_KEY}&page=${page}&query=${this.searchQuery}&include_adult=false&language=en`,
    );
  }
  //–≤—ñ–¥–ø–æ–≤—ñ–¥—å –∂–∞–Ω—Ä–∏ —Ñ—ñ–ª—å–º—ñ–≤—É –≤—ñ–¥–ø–æ–≤—ñ–¥—ñ –ø–∏—à–µ–º –≤ –º–∞—Å–∏–≤
  getGenresMovies() {
    return axios
      .get(`${BASE_URL}3/genre/movie/list?api_key=${API_KEY}&language=en-US`)
      .then(({ data: genres }) => {
        genres.genres.forEach(element => {
          this.genresArr.push(element);
        });
        refs.genreSelector.insertAdjacentHTML(
          'beforeend',
          filterTmpl(this.genresArr),
        );
      });
  }
  // –ø–æ–ª—É—á–∞–µ–º id —Ñ—ñ–ª—å–º–∞ –≤—ñ–¥–¥–∞–µ–º —ñ–Ω—Ñ—É –ø—ñ—Å–ª—è –∫–ª—ñ–∫–∞ –ø–æ –∫–∞—Ä—Ç–æ—á—Ü—ñ
  //—Ñ–∞–∫—Ç–∏—á–Ω–æ –Ω–µ–ø–æ—Ç—Ä—ñ–±–Ω–æ –ø—Ä–æ–≥–∞–Ω—è—Ç–∏ —á–µ—Ä–µ–∑ —Ñ—É–Ω–∫—Ü—ñ—é –∫–æ—Ä–µ–∫—Ü—ñ—ó –ª–∏—à –ø–æ–ø—Ä–∞–≤–∏—Ç–∏ –∂–∞–Ω—Ä–∏ —ñ –≤—Å–µ
  //—Å—Ç–∞—Ä–∏–π –≤–∞—Ä—ñ–∞–Ω—Ç
  // getResponseInfo(id) {
  //   return axios.get(
  //     `${BASE_URL}3/movie/${id}?api_key=${API_KEY}&language=en-US`,
  //   );
  // }
  async getResponseInfo(currentId) {
    //—â–æ–± –Ω–µ —Ä–æ–±–∏—Ç–∏ —â–µ –æ–¥–∏–Ω –∑–∞–ø—Ä–æ—Å —è–∫—â–æ –ø–æ —Ç–æ–º—É —Å–∞–º–æ–º—É —Ñ—ñ–ª—å–º—É –∫–ª—ñ–∫–Ω—É–ª–∏
    //–≤—ñ–¥–¥–∞—î–º –æ–±—î–∫—Ç —ñ–∑ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞ –∫–ª–∞—Å—É
    if (this.movieId === currentId) return this.movie;
    //–ø–∏—à–µ–º –≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –æ–±—î–∫—Ç —è–∫—â–æ –≤—ñ–¥–ø–æ–≤—ñ–¥—å —É—Å–ø—ñ—à–Ω–∞
    //–æ–±–Ω—É–ª–µ–Ω–Ω—è –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä–∞
    this.movieId = currentId;
    this.movie = {};
    try {
      const { data } = await axios.get(
        `${BASE_URL}3/movie/${currentId}?api_key=${API_KEY}&language=en-US`,
      );
      const { genres } = data;
      const newGenres = genres.map(({ name }) => name); // –ø–µ—Ä–µ–∑–∞–ø–∏—Å—É—î–º –ø–æ –Ω–æ—Ä–º–∞–ª—å–Ω–æ–º—É –∂–∞–Ω—Ä–∏
      data.genres = [...newGenres];
      //–∑–∞–ø–∏—Å –æ–±—î–∫—Ç–∞ –≤ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä
      this.movie = { ...data };
      return data;
    } catch (error) {
      newNotification.errorObject();
    }
  }
  getTrailer(movie_id) {
    return axios.get(
      `${BASE_URL}3/movie/${movie_id}/videos?api_key=${API_KEY}&language=en-US`,
    );
  }
  getMoviesByGenre(newPage) {
    let page = this.page;
    if (newPage) page = newPage;
    let str = '';
    if (this.genreCriterion && !this.sortByCriterion) {
      str = `&with_genres=${this.genreCriterion}`;
    }
    if (this.sortByCriterion && !this.genreCriterion) {
      str = `&sort_by=${this.sortByCriterion}`;
    }
    if (this.genreCriterion && this.sortByCriterion) {
      str = `&with_genres=${this.genreCriterion}&sort_by=${this.sortByCriterion}`;
    }
    return axios.get(
      `${BASE_URL}3/discover/movie?api_key=${API_KEY}&page=${page}&language=en-US${str}`,
    );
  }
  get query() {
    return this.searchQuery;
  }
  set query(newQuery) {
    this.searchQuery = newQuery;
  }
}
const newApi = new MoviesApiServiceVersion();
export { newApi };
